export function useTrades(symbol: string) {

  const [trades, setTrades] = useState<TradesData>()

  const api = useAuthNoKeyApiInstance()

  const fetchData = () => {
    if (!api) {
      return undefined
    }
    return api.getMarketTradeWrapper(symbol)
  }

  usePromiseJobInTimer(fetchData, setTrades, 'useTrades', [api, symbol])

  return {
    trades,
  }

}

export function useTickerMap(market: string) {

  const [tickerMap, setTickerMap] = useState<{[key: string]: TickerData}>()

  const api = useAuthNoKeyApiInstance()

  const fetchData = () => {
    if (!api) {
      return undefined
    }
    return api.getTickerWrapper([market])
  }

  usePromiseJobInTimer(fetchData, setTickerMap, 'useTickerMap', [api, market])

  return {
    tickerMap,
  }

}

export function useDepth() {

  const { tradingInfo } = useGetTradingInfo()

  const api = useAuthNoKeyApiInstance()

  const [depth, setDepth] = useState<DepthData>()

    const fetchData = () => {
      if (!api) {
        return undefined
      }
      return api.getDepthWrapper(tradingInfo.symbol, 0, 20)
    }
  
    usePromiseJobInTimer(fetchData, setDepth, 'useDepth', [api, tradingInfo.symbol])

  return {
    depth,
  }

}
