import React, { useState } from 'react'

import styled from 'styled-components/macro'

import styles from './lng_selector.module.scss'

import { useTheme } from 'styled-components'

import IconBtn from 'components/btns/IconBtn'

export interface LngSelectorProps {
  lng_data: any[],
  onLngChange?: any,
}

const LngSelectMain = styled.div`
  float: right;
  margin-left: 28px;
`

const DropDownContainer = styled.div`
  width: 20px;
  z-index: 1;
`

const DropDownHeader = styled.div`
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.15);
`

const DropDownListContainer = styled.div``

const DropDownList = styled.ul`
  background: ${({ theme }) => theme.colors.bodyBgColor};
  width: 80px;
  padding: 0;
  margin: 0;
  padding-left: 5px;
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
  color: ${({ theme }) => theme.colors.cwBtnColor};
  font-weight: 500;
  &:first-child {
    padding-top: 2px;
  }
`

const ListItem = styled.li`
  cursor: pointer;
  list-style: none;
  margin-bottom: 2px;
`

const LngSelector = ({ lng_data, onLngChange, }: LngSelectorProps) => {

  const theme: any = useTheme()

  return (
    <LngSelectMain>
      <DropDownContainer className={styles.dropdown}>
        <DropDownHeader>
          <IconBtn marginLeft={'0px'} src={theme.imgs.lang_img} w={'20px'} h={'20px'} />
        </DropDownHeader>
        <DropDownListContainer className={styles.dropdown_content}>
          <DropDownList>
            {
              lng_data.map((item: any, index: number) => {
                return (<ListItem key={index} value={item['value']} onClick={() => { onLngChange(item['value']) }}>{item['title']}</ListItem>)
              })
            }
          </DropDownList>
        </DropDownListContainer>
      </DropDownContainer>
    </LngSelectMain>
  )
}

export default LngSelector