import styled from 'styled-components/macro'

export interface CircleProps {
  color?: string,
  float?: string,
  diameter?: string,
}

export const Circle = styled.div<CircleProps>`
  border-radius: 50%;
  background-color: ${props => (props.color ? props.color : '#FFFFFFFF')};
  float: ${props => (props.float ? props.float : 'left')};
  width: ${props => (props.diameter ? props.diameter : '20px')};
  height: ${props => (props.diameter ? props.diameter : '20px')};
`
