import React from 'react'
import styled from 'styled-components/macro'

import BigNumber from 'bignumber.js'
import { getLocaleDtFromTs } from 'utils/dt_tools'

const Styles = styled.div`

  overflow-y: scroll;
  height: 100%;
  
  &::-webkit-scrollbar {
    display:none;
  }

  table {
    border-spacing: 0;
  
    height: 500px;

    tr {
      :last-child {
        td {
          border-bottom: 0;
        }
      }
    }

    th,
    td {
      margin: 0;
      padding-top: 3px;

      text-align: right;

      :last-child {
      }
    }

    .ask_row {
      color: #00BBA8;
    }

    .bid_row {
      color: #FF5677;
    }

  }
`
export interface TradeRecordTableProps {
  trades: any
  cols: any
}

const getAmt = (rawAmt: any, precision: number = 4) => {
  return new BigNumber(rawAmt).dividedBy(1e+18).toNumber().toFixed(precision)
}

const TradeRecordTable = ({ trades, cols }: TradeRecordTableProps) => {

  return (
    <Styles>
      <table>
        <thead>
          <tr>
            {
              cols.map((col: any, trade_index: number) => {
                return (
                  <th key={trade_index}>
                    {col.Header}
                  </th>
                )
              })
            }
          </tr>
        </thead>
        <tbody>
          {
            trades && trades.trades &&
            trades.trades.map((trade: any, trade_index: number) => {
              return (
                <tr key={trade_index}>
                  <td>{trade[4]}</td>
                  <td>{(parseInt(trade[3]) / 1e18).toFixed(4)}</td>
                  <td>{getLocaleDtFromTs(trade[0])}</td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </Styles>
  )
}

export default TradeRecordTable
