import React from 'react'
import styled from 'styled-components'

import { useSystem } from 'stores/system/hook'

const SystermError = styled.div`
  color: #ffffff;
  min-height: 600px;
`

export interface SystemErrorProps {
  children: React.ReactNode
}

const SystemError = ({ children }: SystemErrorProps) => {

  const sys = useSystem()

  return (
    <React.Fragment>
      {
        sys.isOKay ? (<>{children}</>) :
          (<SystermError>
            System Error Show Page
          </SystermError>)
      }
    </React.Fragment>
  )

}

export default SystemError
