import styled from 'styled-components/macro'

export interface IconBtnProps {
  src?: any
  w?: string
  h?: string
  marginTop?: string
  marginLeft?: string
  onClick?: any
}

const BtnImg = styled.img<IconBtnProps>`
  cursor: pointer;
  margin-top: ${({ marginTop }) => marginTop ? marginTop : 0};
  margin-left: ${({ marginLeft }) => marginLeft ? marginLeft : 0};
`

const IconBtn = ({ onClick, src, w, h, marginLeft, marginTop }: IconBtnProps) => {
  return (
    <BtnImg marginLeft={marginLeft} marginTop={marginTop} width={w} height={h} src={src} onClick={onClick}>
    </BtnImg>
  )
}

export default IconBtn
