import ConnectWalletBtnUI, { ConnectWalletBtnProps } from 'components/btns/ConnectWalletBtn/ConnectWalletBtn'
import { useTranslation } from 'react-i18next'

import { useModal, } from 'stores/modal_new/hook'

import WalletModal from 'features/components/modal/WalletModal'

const ConnectWalletBtn = ({ w, h, }: ConnectWalletBtnProps) => {

  const { t } = useTranslation()

  const modalHook = useModal()

  return (
    <>
      <ConnectWalletBtnUI onClick={modalHook.toggleModal} txt={t('common:connect_btn')} w={w} h={h} />
      <WalletModal isOpen={modalHook.isOpen} toggleModal={modalHook.toggleModal}/>
    </>
  )
}

export default ConnectWalletBtn
