import { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import { useAuthNoKeyApiInstance, useAuthWsApiInstance, } from '@loopring-web/sdk/hooks/common/useApi'

import { RootState } from 'stores'

import { setMarkets, setExchangeInfo, setTokens, setWsApiKey, } from 'stores/trading/reducer'

export function useGetTradingInfo() {
    const tradingInfo = useSelector((state: RootState) => state.trading)
    return {
        tradingInfo,
        exchangeInfo: tradingInfo.exchangeInfo,
        markets: tradingInfo.markets,
        tokens: tradingInfo.tokens,
        wsApiKey: tradingInfo.wsApiKey,
    }
}

export function useUpdateExchangeMarkets() {
    const dispatch = useDispatch()
    const api = useAuthNoKeyApiInstance()

    useEffect(() => {

        if (!api) {
            return
        }

        api.getMarketsWrapper().then((response: any) => {
    
            dispatch(setMarkets(response))
    
        }, (reject: any) => {
            console.log(reject)
        })

    }, [api])

}

