import { useRef } from 'react'

import Qrcode from 'components/qrcodes/Qrcode'

import DepositBtn from 'features/components/btns/DepositBtn'

import { useAccount } from 'stores/account/hook'

import { useGetAmmPools, useGetGas } from 'hooks/exchange/useAuthNoKeyApi'

import { useGetTradingInfo } from 'stores/trading/hook'
import { useActiveWeb3React } from '@loopring-web/sdk/hooks/web3/useWeb3'
import { useAmmpoolWs } from '@loopring-web/sdk/hooks/common/useWebsocketApi'

const DebugPage = () => {

  const inputRef = useRef<HTMLInputElement>(null)

  const { account, isNoAccount, apiKey } = useAccount()

  const {socketData} = useAmmpoolWs(apiKey)

  const { markets, exchangeInfo, tokens, } = useGetTradingInfo()

  const { gas } = useGetGas()

  const gasPrice = gas?.data.data

  const { ammpools } = useGetAmmPools()

  const deposit = async () => {
    const val = inputRef.current?.value
    const token = 'LRC'

    console.log('gas=', gasPrice)
    console.log('val=', val)
    const tokenInfo = tokens[token]

    console.log('tokenInfo:', tokenInfo)
  }

  const showAddr = account.accAddr ? account.accAddr : ''

  return (
    <div>
      <div>account:{JSON.stringify(account)}</div>
      <div>eddsaKey:{account.eddsaKey}</div>
      <div>wsdata: {JSON.stringify(socketData)}</div>
      <div>----------------------------</div>
      <div>markets:{JSON.stringify(markets)}</div>
      <div>----------------------------</div>
      <div>depositAddress:{exchangeInfo?.depositAddress}</div>
      <div>exchangeAddress:{exchangeInfo?.exchangeAddress}</div>
      <div>----------------------------</div>
      {/*<div>exchangeInfo:{JSON.stringify(exchangeInfo)}</div>
      <div>----------------------------</div>*/}
      <div>tokens:{JSON.stringify(tokens)}</div>
      <div>----------------------------</div>

      {isNoAccount() && <Qrcode value={showAddr} size={300} />}
      <input type='input' ref={inputRef} width="150px" />
      <DepositBtn onClick={deposit} />
    </div>
  )

}

export default DebugPage
