import { Link } from 'react-router-dom'

import styled from 'styled-components/macro'
import styles from './symbollist.module.scss'

const SymbolSelector = styled.select`
`

export interface SymbolListProps {
    listData: any
    location: any
}

const SymbolList = ({ listData, location, }: SymbolListProps) => {

    return (<SymbolSelector className={styles.dropdown_content}>
        {
            listData.map((sub_value: any, sub_index: number) => {
                return (
                    <option key={sub_index} value={location.pathname + sub_value['link']}>{sub_value['title']}</option>
                )
            })
        }
    </SymbolSelector>)
}

export default SymbolList
