import React from 'react'
import styled from 'styled-components/macro'

import BigNumber from 'bignumber.js'
import { useTable } from 'react-table'
import { getLocaleDtFromTs } from 'utils/dt_tools'

const Styles = styled.div`

  overflow-y: scroll;
  height: 100%;
  
  &::-webkit-scrollbar {
    display:none;
  }

  table {
    border-spacing: 0;
  
    height: 500px;

    tr {
      :last-child {
        td {
          border-bottom: 0;
        }
      }
    }

    th,
    td {
      margin: 0;
      padding-top: 3px;

      text-align: right;

      :last-child {
      }
    }

    .ask_row {
      color: #00BBA8;
    }

    .bid_row {
      color: #FF5677;
    }

  }
`
export interface OrderRecordTableProps {
  orders: any
  cols: any
}

const getAmt = (rawAmt: any, precision: number = 4) => {
  return new BigNumber(rawAmt).dividedBy(1e+18).toNumber().toFixed(precision)
}

const OrderRecordTable = ({ orders, cols }: OrderRecordTableProps) => {

  return (
    <Styles>
      <table>
        <thead>
          <tr>
            {
              cols.map((col: any, order_index: number) => {
                return (
                  <th key={order_index}>
                    {col.Header}
                  </th>
                )
              })
            }
          </tr>
        </thead>
        <tbody>
          {
            orders && orders.orders &&
            orders.orders.map((order: any, order_index: number) => {
              return (
                <tr key={order_index}>
                  <td>{getLocaleDtFromTs(order.validity.start * 1000)}</td>
                  <td>{order.market}</td>
                  <td>{order.orderType}</td>
                  <td>{order.side}</td>
                  <td>{order.price}</td>
                  <td>{order.volumes.quoteAmount}</td>
                  <td>{order.status}</td>
                  <td></td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </Styles>
  )
}

export default OrderRecordTable
