import styled from 'styled-components/macro'

export interface TabProps {
    active: boolean
    isRight? : boolean
}

export const Tabs = styled.div`
  overflow: hidden;
  background: #2A2D34;
  height: 40px;
`

export const Tab = styled.button<TabProps>`
  float: ${props => (props.isRight ? 'right' : 'float')};
  border: none;
  outline: none;
  cursor: pointer;
  width: 120px;
  // position: relative;

  margin-right: 0.1em;
  font-size: 1em;
  border-top: ${props => (props.active ? '2px solid #5773FF' : '')};
  color: ${props => (props.active ? '#FFFFFF' : '#76808F')};
  background-color: ${props => (props.active ? '#181C20' : '#2A2D34')};
  height: ${props => (props.active ? '40px' : '36px; top:4px')};
  transition: background-color 0.5s ease-in-out;

  :hover {
    border-top: 2px solid #5773FF;
    color: #FFFFFF;
    background-color: #181C20;
  }
`

export const Content = styled.div<TabProps>`
  height: 100%;
  ${props => (props.active ? '' : 'display:none;')}
`
