import { useEffect, useState, useRef, } from 'react'
import { useAuthNoKeyApiInstance, useAuthApiKeyApiInstance, useAuthWsApiInstance, useAuthHasEddsaSigApiInstance, } from '@loopring-web/sdk/hooks/common/useApi'
import { useGetTradingInfo } from 'stores/trading/hook'
import { useGetAmmPools, useGetAmmPoolsBalances, useGetGas } from 'hooks/exchange/useAuthNoKeyApi'

import styled from 'styled-components/macro'

import { getBalances, getAmmInfo, getRight, getAmountOut, applySlippageTolerance, getTokenInfoByToken, getToken, toWEI, } from 'utils/swap_calc_utils'
import { useAccount } from 'stores/account/hook'
import BigNumber from 'bignumber.js'
import { dumpError400 } from 'utils/network_tools'
import { useGetUserBalances } from 'hooks/exchange/useAuthKeyApi'

const Selector = styled.select`
`

const Swap = styled.button`
background-color: white
`

const SwapPage = () => {

  const [priceImpact, setPriceImpact] = useState(0)

  const [slippage, setSlippage] = useState<number | undefined>()
  const [slippageOut, setSlippageOut] = useState<number | string>()

  const [baseAmt, setBaseAmt] = useState<string | number>()

  const [quoteAmt, setQuoteAmt] = useState<string | number>()

  const api = useAuthNoKeyApiInstance()

  const authKeyApi = useAuthApiKeyApiInstance()

  const eddsaApi = useAuthHasEddsaSigApiInstance()

  // const [markets, setMarkets] = useState<any>({})

  const [base, setBase] = useState('')

  const [quote, setQuote] = useState('')

  const [left, setLeft] = useState<any>([])

  const [right, setRight] = useState<any>([])

  const { exchangeInfo, tokens, } = useGetTradingInfo()

  const { ammpools, } = useGetAmmPools()

  const { ammPoolsBalances } = useGetAmmPoolsBalances()

  const { apiKey, eddsaKey, accountId, } = useAccount()

  const [takerRate, setTakerRate] = useState(10)

  const [currentPrice, setCurrentPrice] = useState<number>(0)
  const [amount1ApplyOrderFee, setAmount1ApplyOrderFee] = useState(0)

  const baseUserBalance = useGetUserBalances(tokens, base)

  const quoteUserBalance = useGetUserBalances(tokens, quote)

  const handleQuoteChange = (event: any) => {
    const quote = event.target.value
    console.log('handleQuoteChange quote:', quote)
    setQuote(quote)
  }

  const handleBaseChange = (event: any) => {
    if (!ammpools) {
      return
    }

    setBase(event.target.value)
    const right: any = getRight(ammpools.pairs, event.target.value)
    setRight(right)
    if (right.length > 0) {
      setQuote(right[0])
    } else {
      setQuote('')
    }

    setBaseAmt('')
    setQuoteAmt('')
    setSlippage(0)
    setSlippageOut(0)
    setPriceImpact(0)
  }

  const onSwap = () => {
    console.log('onSwap, tokens:', tokens)
    console.log('try to swap ', baseAmt, base, ' -> ', quoteAmt, quote)

    const swap = async () => {

      console.log(tokens)
      console.log(exchangeInfo)

      console.log('apiKey:', apiKey)

      console.log('eddsaKey:', eddsaKey)

      const {
        feeBips,
      } = getAmmInfo(ammpools.ammpools, ammPoolsBalances, base, quote)

      const baseToken = getToken(tokens, base)
      const quoteToken = getToken(tokens, quote)

      const allOrNone = false
      const fillAmountBOrS = false

      const storageId = await authKeyApi.getNextStorageId(accountId, baseToken.tokenId, apiKey)
      console.log('storageId:', storageId)

      const orderId: number = storageId.orderId

      const validUntil = Math.ceil(new Date().getTime() / 1000) + 3600 * 1000
      // const validUntil = 1700000000

      console.log('exchangeAddress:', exchangeInfo?.exchangeAddress)

      if (!exchangeInfo?.exchangeAddress || !baseAmt || !quoteAmt) {
        throw Error('no exchangeAddress!')
      }

      console.log('baseAmt:', baseAmt)
      console.log('amount1ApplyOrderFee:', amount1ApplyOrderFee)
      const amount0InBigNumber = toWEI(tokens, base, baseAmt)
      const amount1WithSlippage = applySlippageTolerance(
        tokens,
        quote,
        amount1ApplyOrderFee,
      )

      const amount1InBigNumber = new BigNumber(amount1WithSlippage.outInWei).toFixed()

      try {

        await eddsaApi.submitOrderV3(eddsaKey, apiKey, exchangeInfo?.exchangeAddress, orderId, accountId,
          baseToken.tokenId, amount0InBigNumber, quoteToken.tokenId, amount1InBigNumber, allOrNone,
          fillAmountBOrS, validUntil, feeBips * 3)

      } catch (reason) {
        dumpError400(reason, 'submitOrderV3')
      }

    }

    swap()

  }

  const onBaseChange = (event: any) => {
    const baseAmt = event.target.value

    const {
      quoteAmt,
      _amount1ApplyOrderFee,
      priceImpact,
    } = getAmountOut(baseAmt, ammpools.ammpools, ammPoolsBalances, tokens, base, quote, takerRate, currentPrice)

    setBaseAmt(baseAmt)
    setQuoteAmt(quoteAmt)
    setAmount1ApplyOrderFee(_amount1ApplyOrderFee)
    setPriceImpact(priceImpact)

  }

  const onQuoteChange = (event: any) => {
    const quoteAmt = event.target.value
    /*

    const {
      amountIn,
      amountInShown,
    } = getAmountIn(quoteAmt, ammpools, ammPoolsBalances, tokens, base, quote)

    setBaseAmt(amountInShown)
    setQuoteAmt(quoteAmt)

    const {
      outInWei,
      out,
    } = applySlippageTolerance(tokens, quote, quoteAmt)

    setSlippage(outInWei.toNumber())
    setSlippageOut(out)
    */

  }

  useEffect(() => {

    if (!ammpools) {
      return
    }

    console.log('reset useEffect!', ammpools)

    const keys = Reflect.ownKeys(ammpools.pairs)
    const vals = ammpools.pairs[keys[0]]

    setLeft(keys)
    setRight(vals.tokenList)
    setBase(keys[0] as string)
    setQuote(vals.tokenList[0])

  }, [ammpools])

  useEffect(() => {

    if (!base || !quote || !ammpools || !ammPoolsBalances || !tokens) {
      return
    }

    const { baseBalance,
      quoteBalance, } = getBalances(ammpools.ammpools, ammPoolsBalances, base, quote, tokens)

    if (baseBalance.volume > 0) {
      setCurrentPrice(quoteBalance.volume / baseBalance.volume)
    }
    else {
      setCurrentPrice(0)
    }

  }, [base, quote, ammpools, ammPoolsBalances, tokens])


  useEffect(() => {

    if (!authKeyApi || !accountId || !apiKey || !base || !quote || !ammpools || !ammPoolsBalances) {
      return
    }

    const { ammInfo } = getBalances(ammpools.ammpools, ammPoolsBalances, base, quote, tokens)

    authKeyApi.getUserFeeRates(accountId, ammInfo.market, apiKey).then((response) => {
      setTakerRate(response[0].takerRate)
    })

  }, [authKeyApi, accountId, apiKey, base, quote, ammpools, ammPoolsBalances])

  /*
  useEffect(() => {

    if (!api) {
      return
    }

    api.getMarketsWrapper().then((markets: any) => {

      if (markets) {

        const keys = Reflect.ownKeys(markets.pairs)
        const vals = markets.pairs[keys[0]]

        setMarkets(markets)
        setLeft(keys)
        setRight(vals.tokenList)
        setBase(keys[0] as string)
        setQuote(vals.tokenList[0])
      }

    }, (reject: any) => {
      console.log(reject)
    })

  }, [api])
  */

  return (
    <>
      <div>
        <Selector onChange={handleBaseChange}>
          {
            left.map((sub_value: any, sub_index: number) => {
              return (
                <option key={sub_index} value={sub_value}>{sub_value}</option>
              )
            })
          }
        </Selector>
        <input type='input' value={baseAmt || ''} width="150px" onChange={onBaseChange} />
        <div>baseUserBalance:{baseUserBalance.balance}</div>
        <div>baseUserBalanceShow:{baseUserBalance.balanceShow}</div>
      </div>

      <div>
        <Selector onChange={handleQuoteChange}>
          {
            right.map((sub_value: any, sub_index: number) => {
              return (
                <option key={sub_index} value={sub_value}>{sub_value}</option>
              )
            })
          }
        </Selector>
        <input type='input' value={quoteAmt || ''} width="150px" onChange={onQuoteChange} />
        <div>quoteUserBalance:{quoteUserBalance.balance}</div>
        <div>quoteUserBalanceShow:{quoteUserBalance.balanceShow}</div>
      </div>

      <div>show CURRENT: {base}-{quote}</div>
      <div>slippage:{slippage}</div>
      <div>slippageOut:{slippageOut}</div>
      <div>currentPrice:{currentPrice}</div>
      <div><Swap onClick={onSwap}>Swap</Swap></div>
      <div>accountId:{accountId}</div>
      <div>exchangeAddress:{exchangeInfo?.exchangeAddress}</div>
      <div>eddsaKey:{eddsaKey}</div>
      <div>apiKey:{apiKey}</div>

      {/*
      <div>tokens:{JSON.stringify(tokens)}</div>*/
      }
    </>
  )

}

export default SwapPage
