import {
  HashRouter as Router,
  Switch,
  Route,
  useParams,
  useRouteMatch,
  Redirect,
} from 'react-router-dom'

import styled from 'styled-components/macro'

import Header from 'layouts/header'
import Footer from 'layouts/footer'

import HomePage from 'pages/HomePage'

import QuotePage from 'pages/QuotePage'
import SwapPage from 'pages/SwapPage'
import TradingPage from 'pages/TradingPage'
import LiquidityPage from 'pages/LiquidityPage'
import L2WalletPage from 'pages/L2WalletPage'
import SystemError from 'features/wrapper/SystemError'
import Redirector from 'features/wrapper/Redirector'
import DevWrapper from 'features/wrapper/DevWrapper'

import DebugPage from 'pages/DebugPage'

const Container = styled.div`
width: 100%;
background: ${({ theme }) => theme.colors.bodyBgColor};
color: #ffffff;
}

`

const InternalContainer = styled.div`
width: 1280px;
margin: 0 auto;
min-height: 600px;
border: 1px solid #555555;

@media screen and (max-width: 1280px) {
    width: 100%;
}

`

const RouterView = () => {

  return (
    <Router>
      <Switch>
        <Route exact path='/'>
          <Header />
          <Container>
            <InternalContainer>
              <HomePage />
            </InternalContainer>
          </Container>
          <Footer />
        </Route>
        <Route exact path='/quote'>
          <Header />
          <Container>
            <InternalContainer>
              <SystemError><QuotePage /></SystemError>
            </InternalContainer>
          </Container>
          <Footer />
        </Route>
        <Route exact path='/trading/quick'>
          <Header />
          <Container>
            <InternalContainer>
              <SystemError><SwapPage /></SystemError>
            </InternalContainer>
          </Container>
          <Footer />
        </Route>
        <Route exact path='/trading/pro'>
          <Header />
          <Container>
            <SystemError><TradingPage /></SystemError>
          </Container>
          <Footer />
        </Route>
        <Route exact path='/liquidity'>
          <Header />
          <Container>
            <InternalContainer>
              <SystemError><LiquidityPage /></SystemError>
            </InternalContainer>
          </Container>
          <Footer />
        </Route>
        <Route exact path='/l2wallet'>
          <Redirector>
            <Header />
            <Container>
              <InternalContainer>
                <SystemError><L2WalletPage /></SystemError>
              </InternalContainer>
            </Container>
            <Footer />
          </Redirector>
        </Route>

        <DevWrapper>
          <Route exact path='/debug'>
            <Header />
            <Container>
              <InternalContainer>
                <DebugPage />
              </InternalContainer>
            </Container>
            <Footer />
          </Route>
        </DevWrapper>

      </Switch>
    </Router>
  )
}

export default RouterView
