import i18 from 'i18next'
import { initReactI18next } from 'react-i18next'
import XHR from 'i18next-xhr-backend'
import LanguageDetector from 'i18next-browser-languagedetector'

import zhCommon from 'locales/zh_CN/common'
import enCommon from 'locales/en_US/common'

import zhMenu from 'locales/zh_CN/menu'
import enMenu from 'locales/en_US/menu'

import zhHome from 'locales/zh_CN/home_page'
import enHome from 'locales/en_US/home_page'

import zhTradingPage from 'locales/zh_CN/trading_page'
import enTradingPage from 'locales/en_US/trading_page'

import zhFooter from 'locales/zh_CN/footer'
import enFooter from 'locales/en_US/footer'

import { LangType } from 'defs/common_defs'

const resources = {
  zh_CN: {
    common: {
      ...zhCommon,
      ...zhMenu,
    },
    pages: {
      ...zhHome,
      ...zhTradingPage
    },
    footer: {
      ...zhFooter,
    }
  },
  en_US: {
    common: {
      ...enCommon,
      ...enMenu,
    },
    pages: {
      ...enHome,
      ...enTradingPage
    },
    footer: {
      ...enFooter,
    }
  },
}

const defLng = LangType.en

i18
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    resources,
    fallbackLng: defLng,
    preload: [defLng],

    keySeparator: false, // we do not use keys in form messages.welcome

    interpolation: {
      escapeValue: false, // react already safes from xss
    },
  })

export default i18
