import { createSlice, PayloadAction } from '@reduxjs/toolkit'

import { MODAL_TYPE } from 'defs/common_defs'

interface ModalInfo {
  type: MODAL_TYPE
  opacity: number
}

const initialState = { type: MODAL_TYPE.UNKNOWN, opacity: 0 } as ModalInfo

const modalSlice = createSlice({
  name: 'modal',
  initialState,
  reducers: {
    setModalType(state, action: PayloadAction<MODAL_TYPE>) {
      state.type = action.payload
    },
    setOpacity(state, action: PayloadAction<number>) {
      state.opacity = action.payload
    },
  },
})

export const { setModalType, setOpacity } = modalSlice.actions
export default modalSlice
