import { Link } from 'react-router-dom'

import { useTheme } from 'styled-components'
import styled from 'styled-components/macro'

import { SystemStatus, Theme, } from 'defs/common_defs'

import { useWeb3Account, useDisconnect, } from 'stores/account/hook'
import { useSystem, } from 'stores/system/hook'
import { useSettings } from 'stores/settings/hook'

import Menu from 'features/components/menu/NormalMenu'
import AccountBtn from 'components/btns/AccountBtn'
import ConnectWalletBtn from 'features/components/btns/ConnectWalletBtn'
import IconBtn from 'components/btns/IconBtn'
import LngSelector from 'features/components/selects/LngSelector'

import { Circle } from 'components/shapes/Circle'

const HeaderDiv = styled.div`
  width: 100%;
  height: 110px;
  background-color: ${({ theme }) => theme.colors.headerBgColor};

  .menu_bar {
    width: 1280px;
    margin:0 auto;
    height: 60px;
  }

  .small_menu_bar {
    display: none;
  }

  .advimg {
    width: 1280px;
    margin:0 auto;
    height: 50px;
    display: block;
  }
  
  .iconbtns {
      float: right;
      margin-right: 5px;
      margin-top: 25px;
  }
  
  .status {
      float: left;
      margin-top: 8px;
  }
  
  .cwbtn {
      float: right;
      margin-right: 0px;
      margin-top: 15px;
  }
  
  @media screen and (max-width: 1280px) {
  
    .menu_bar {
      width: 100%;
    }
  
    .advimg {
      width: 100%;
    }
  
  }
  
  @media screen and (max-width: 880px) {
  
    .menu_bar {
      display: none;
    }
  
    .small_menu_bar {
      display: block;
    }
  
    .advimg {
      width: 100%;
    }
  
  }

`

const LogoutBtn = styled.button`
    cursor: pointer;
    color: #ffffff;
    width: 60px;
    height: 36px;
    line-height: 36px;
    border-radius: 15px;
    border: 2px solid #8AC007;
`

const AdvDiv = styled.div`
  width: 100%;
  height: 50px;
  text-align: center;
  background-color: #5773FF;
`

const AdvImg = styled.img`
`

const Logo = styled.img`
  width: 150px;
  height: 50px;
  float: left;
`

const Header = () => {

  const system = useSystem()

  const settings = useSettings()

  const theme: any = useTheme()

  const { account, isConnected, } = useWeb3Account()

  const { disconnect } = useDisconnect()

  const size = '20px'

  const onDownload = () => {
    console.log('onDownload')
    if (system.system.status === SystemStatus.ERROR) {
      system.setSystemStatusWrapper(SystemStatus.NORMAL)
    } else {
      system.setSystemStatusWrapper(SystemStatus.ERROR)
    }
  }

  const onNotification = () => {
    console.log('onNotification')
  }

  const onModeChange = () => {
    console.log('onModeChange')
    if (settings.themeType === Theme.dark) {
      settings.setThemeWrapper(Theme.light)
    } else {
      settings.setThemeWrapper(Theme.dark)
    }
  }

  return (
    <HeaderDiv>
      <AdvDiv><AdvImg className={'advimg'} src={theme.imgs.advImg}></AdvImg></AdvDiv>
      <div className={'menu_bar'}>
        <div><Link to='/'><Logo src={theme.imgs.logo} /></Link></div>
        <Menu />
        <div className={'iconbtns'}>
          <IconBtn marginLeft={'48px'} onClick={onDownload} src={theme.imgs.download_img} w={size} h={size} />
          <IconBtn marginLeft={'28px'} onClick={onNotification} src={theme.imgs.notification_img} w={size} h={size} />
          <IconBtn marginLeft={'28px'} onClick={onModeChange} src={theme.imgs.mode_img} w={size} h={size} />
          <LngSelector />
        </div>
        <div className={'cwbtn'}>
          {
            (isConnected) ? (
              <>
                <AccountBtn txt={account} w={'150px'} />
                <div className={'status'}><Circle color={'#00ff00'} /></div>
                <LogoutBtn onClick={disconnect}>Logout</LogoutBtn>
              </>
            )
              :
              <>
                <div className={'status'}><Circle color={'#ff0000'} /></div>
                <ConnectWalletBtn />
              </>
          }
        </div>
      </div>

      <div className={'small_menu_bar'}>
        <div><Link to='/'><Logo src={theme.imgs.logo} /></Link></div>
        <div className={'iconbtns'}>
          <IconBtn marginLeft={'48px'} onClick={onDownload} src={theme.imgs.download_img} w={size} h={size} />
          <IconBtn marginLeft={'28px'} onClick={onNotification} src={theme.imgs.notification_img} w={size} h={size} />
          <IconBtn marginLeft={'28px'} onClick={onModeChange} src={theme.imgs.mode_img} w={size} h={size} />
          <IconBtn marginLeft={'28px'} onClick={onModeChange} src={theme.imgs.lang_img} w={size} h={size} />
        </div>
        <div className={'cwbtn'}>
          <ConnectWalletBtn />
        </div>
      </div>
    </HeaderDiv>
  )
}

export default Header
