import React from 'react'

import { useCheckAccStatus } from 'stores/account/hook'

import { useUpdateWsApiKey, useUpdateExchangeInfo, useUpdateExchangeMarkets, useUpdateTokens, } from 'stores/trading/hook'

export interface AppWrapperProps {
  children: React.ReactNode
}

const AppWrapper = ({ children }: AppWrapperProps) => {

  useUpdateExchangeInfo()
  useUpdateExchangeMarkets()
  useUpdateTokens()
  useUpdateWsApiKey()

  useCheckAccStatus()

  return (
    <React.Fragment>
      { children }
    </React.Fragment>
  )

}

export default AppWrapper
