import { useState, useEffect, useCallback } from 'react'

import { useAccount } from 'stores/account/hook'

import { OrdersData } from '@loopring-web/sdk/defs/exchange_defs'

import { REFRESH_RATE } from '@loopring-web/sdk/defs/exchange_defs'
import { useAuthApiKeyApiInstance } from '@loopring-web/sdk/hooks/common/useApi'
import { dumpError400 } from 'utils/network_tools'

import { getToken, fromWEI } from 'utils/swap_calc_utils'

export function useOrders() {

  const acc = useAccount().account

  const api = useAuthApiKeyApiInstance()

  const [orders, setOrders] = useState<OrdersData>()

  useEffect(() => {

    if (!acc || !api) {
      return
    }

    let mounted = true

    const exit = (id: any) => {
      mounted = false
      clearInterval(id)
    }

    if (acc.accInfo?.accountId > 0) {

      const id: any = setInterval(async () => {
        try {
          if (acc.accInfo) {
            const orders_data = (await api.getOrders(acc.accInfo.accountId, '')).data
            if (mounted) {
              setOrders(orders_data)
            }
          }
        } catch (err) {
          return exit(id)
        }
      }, REFRESH_RATE)

      return exit(id)

    }
  })

  return {
    orders,
  }

}

export function useGetUserBalances(tokens: any, symbol: any) {

  const token = getToken(tokens, symbol)

  const api = useAuthApiKeyApiInstance()

  const [balance, setBalance] = useState<any>()

  const [balanceShow, setBalanceShow] = useState<any>()

  const { apiKey, accountId } = useAccount()

  useEffect(() => {
    if (!api || !apiKey || !accountId || !token) {
      return
    }

    api.getUserBalances(accountId, apiKey, token.tokenId).then((data) => {
      const balance = data[0].total
      setBalance(balance)
      setBalanceShow(fromWEI(tokens, symbol, balance))
    }).catch((reason: any) => {
      dumpError400(reason, 'getUserBalances tokenId:' + tokens.tokenId)
      setBalance(0)
      setBalanceShow(0)
    })

  }, [api, apiKey, accountId, token,])

  return {
    balance,
    balanceShow,
  }

}