import React from 'react'

export interface DevWrapperProps {
  children: React.ReactNode
}

const DevWrapper = ({ children }: DevWrapperProps) => {

  return (
    <React.Fragment>
      {
        process.env.NODE_ENV !== 'production' ? (
          <>
          {children}
          </>
        ) : null
      }
    </React.Fragment>
  )

}

export default DevWrapper
