import styled from 'styled-components/macro'

import StyledModal from 'components/modals/StyledModal'

import { useConnect, useWeb3Account } from 'stores/account/hook'

import { wallets } from 'config/supported_wallets'

const ModalWrapper = styled.div`
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 900px;
  height: 600px;
`

const Item = styled.img`
  cursor: pointer;
  width: 300px;
  height: 300px;

  &:hover {
    background-color: #777777;

  }
`

export interface WalletModalProps {
  isOpen: boolean
  toggleModal: any
}

const WalletModal = ({isOpen, toggleModal}: WalletModalProps) => {
  
  const { connect } = useConnect()

  return (
    <StyledModal
      isOpen={isOpen}
      //afterOpen={walletModalHook.afterOpen}
      //beforeClose={walletModalHook.beforeClose}
      onBackgroundClick={toggleModal}
      onEscapeKeydown={toggleModal}
      // backgroundProps={{opacity}}
    >
      <ModalWrapper>
        {
          wallets.map((item: any, index: number) => {
            // @ts-ignore

            /*
            const activating = currentConnector === activatingConnector
            const connected = currentConnector === connector
            const disabled = !triedEager || !!activatingConnector || connected || !!error
            */

            const onClick = () => {
              toggleModal()
              connect(item.name)
            }

            return <Item key={index} src={item.img} onClick={onClick} />
          })
        }
      </ModalWrapper>
    </StyledModal>
  )

}

export default WalletModal
