import { useTranslation } from 'react-i18next'
import { useLocation } from 'react-router-dom'

import styled from 'styled-components/macro'

import SymbolList from 'components/selects/SymbolList'

import { getLocaleDt } from 'utils/dt_tools'
import { useTickerMap } from 'hooks/exchange/useAuthNoKeyApi'
import { useGetTradingInfo } from 'stores/trading/hook'

const InfoPanel = styled.div`
    padding-top: 20px;

    .row1 {
        float: left;
        margin-left: 28px;
    }

    .row2 {
        margin-top: 10px;
    }
`

const BasicInfoPanel = () => {

    const { t } = useTranslation()

    const location = useLocation()

    const { tradingInfo } = useGetTradingInfo()

    const { tickerMap } = useTickerMap(tradingInfo.symbol)

    const ticker = tickerMap ? tickerMap[tradingInfo.symbol] : undefined

    return (
        <div className='draggable_panel'>
            <InfoPanel>
                <div className={'row1'}><SymbolList location={location} listData={tradingInfo.markets}></SymbolList></div>
                <div className={'row1'}><div>{getLocaleDt(ticker?.date_time)}</div></div>
                <div className={'row1'}><div>{t('pages:basic_24hrs_updown')}</div><div className={'row2'}></div></div>
                <div className={'row1'}><div>{t('pages:basic_24hrs_low')}</div><div className={'row2'}>{ticker?.low}</div></div>
                <div className={'row1'}><div>{t('pages:basic_24hrs_high')}</div><div className={'row2'}>{ticker?.high}</div></div>
                <div className={'row1'}><div>{t('pages:basic_24hrs_volume')}({tradingInfo.symbol_quote})</div><div className={'row2'}>{ticker?.quote_token_amt}</div></div>
                <div className={'row1'}><div>{t('pages:basic_24hrs_volume')}({tradingInfo.symbol_base})</div><div className={'row2'}>{ticker?.base_token_amt}</div></div>
            </InfoPanel>
        </div>
    )
}

export default BasicInfoPanel
