import { useTranslation } from 'react-i18next'

import { Tabs, Tab, Content } from 'components/tabs/HTabs'
import OrderRecordTable from 'components/tables/OrderRecordTable'

import { useOrders } from 'hooks/exchange/useAuthKeyApi'
import { useTabs } from 'hooks/tabHook/useTabs'

const OrderRecordsPanel = () => {

    const { t } = useTranslation()

    const cols = [
        {
            Header: t('pages:order_col_1'),
            accessor: 'order_col_1',
        },
        {
            Header: t('pages:order_col_2'),
            accessor: 'order_col_2',
        },
        {
            Header: t('pages:order_col_3'),
            accessor: 'order_col_3',
        },
        {
            Header: t('pages:order_col_4'),
            accessor: 'order_col_4',
        },
        {
            Header: t('pages:order_col_5'),
            accessor: 'order_col_5',
        },
        {
            Header: t('pages:order_col_6'),
            accessor: 'order_col_6',
        },
        {
            Header: t('pages:order_col_7'),
            accessor: 'order_col_7',
        },
        {
            Header: t('pages:order_col_8'),
            accessor: 'order_col_8',
        },
    ]

    const { orders } = useOrders()

    const { active, handleClick, } = useTabs()

    return (
        <div className='draggable_panel'>
            <div className='draggable_header' >
                {t('pages:order_record_title')}
            </div>

            <Tabs>
                <Tab onClick={handleClick} active={active === '0'} id={'0'}>
                    {t('pages:order_record_current')}
                </Tab>

                <Tab onClick={handleClick} active={active === '1'} id={'1'}>
                    {t('pages:order_record_history')}
                </Tab>
            </Tabs>
            <>
                <Content active={active === '0'}>
                    <OrderRecordTable cols={cols} orders={orders}/>
                </Content>
                <Content active={active === '1'}>
                    <OrderRecordTable cols={cols} orders={orders}/>
                </Content>
            </>
        </div>
    );
};

export default OrderRecordsPanel
