import { useSelector } from 'react-redux'

import { useDispatch } from 'react-redux'

import { RootState } from 'stores'

import { SystemStatus } from 'defs/common_defs'

import { setSystemStatus } from 'stores/system/reducer'

export function useSystem() {

    const system = useSelector((state: RootState) => state.system)

    const dispatch = useDispatch()

    const isOK = system.status === SystemStatus.NORMAL

    const setSystemStatusWrapper = (status: SystemStatus) => {
        dispatch(setSystemStatus(status))
    }

    return {
        system: system, 
        isOKay: isOK,
        setSystemStatusWrapper,
    }

}
