import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { Theme, LangType } from 'defs/common_defs'

export interface SettingsState {
  themeType: string
  lng: string
}

const init_mode = Theme.dark
const init_lang = LangType.en

const initialState = {} as SettingsState

const settingsSlice = createSlice({
  name: 'settings',
  initialState,
  reducers: {
    setTheme(state, action: PayloadAction<string>) {
      state.themeType = action.payload
    },
    setLng(state, action: PayloadAction<string>) {
      state.lng = action.payload
    },
  },
})

export const { setTheme, setLng } = settingsSlice.actions
export default settingsSlice
