import * as sign_tools from '@loopring-web/sdk/lightcone_api/api_wrapper/sign_tools'

import { connectorsByName } from 'defs/web3_defs'

import { ConnectorNames, } from '@loopring-web/sdk/defs/web3_defs'

import { RootState } from 'stores'
import { reset, setAccountStatus, setAccAddr, setConnectName, setConnectNameTemp, setIsContractAddress, setAccountInfo, setEddsaKey, setApikey, } from 'stores/account/reducer'

