import { dumpError400 } from 'utils/network_tools'

export function useWeb3Account() {

    const {
        connector,
        library,
        account,
        chainId,
        active,
        error,
    } = useActiveWeb3React()

    const isConnected: boolean = active && !voca.isBlank(account as string)

    return {
        account,
        connector,
        library,
        chainId,
        active,
        isConnected,
        error,
    }

}

export function useAccount() {
    const account: Lv2Account = useSelector((state: RootState) => state.account)

    const isNoAccount = () => {
        return account.status === AccountStatus.NOACCOUNT
    }

    const isActivated = () => {
        return account.status === AccountStatus.ACTIVATED
    }

    return {
        account,
        isNoAccount,
        isActivated,
        apiKey: account.apiKey,
        eddsaKey: account.eddsaKey,
        accountId: account.accInfo?.accountId,
    }
}

export function useStateMachine() {
    const dispatch = useDispatch()

    const machine = buildMachine(AccountMachineSpec())

    const { account } = useAccount()

    const sendEvent = useCallback((event: StatusChangeEvent) => {
        const nextState = machine(account.status, event)
        if (nextState) {
            dispatch(setAccountStatus(nextState))
        }
    }, [account.status])

    return {
        account,
        sendEvent,
    }
}


export function useConnect() {

    const {
        activate,
    } = useActiveWeb3React()

    const { sendEvent } = useStateMachine()

    const dispatch = useDispatch()

    const [activatingConnector, setActivatingConnector] = useState<any>()

    const connect = useCallback((item_name: ConnectorNames) => {
        const newConnector: any = connectorsByName[item_name]
        setActivatingConnector(newConnector)
        activate(newConnector)
        dispatch(setConnectNameTemp(item_name))
        sendEvent(StatusChangeEvent.Connecting)
    }, [])

    return {
        connect,
        activatingConnector,
    }

}

export function useDisconnect() {

    const {
        deactivate,
    } = useActiveWeb3React()

    const dispatch = useDispatch()

    const disconnect = useCallback(() => {
        deactivate()
        dispatch(reset())
    }, [deactivate, dispatch])

    return {
        disconnect,
    }

}

function checkAccountAvailable(nokeyApi: AuthNoKeyApi, ecdsaApi: AuthHasEcdsaSigApi, eddsaApi: AuthHasEddsaSigApi, account: Lv2Account, dispatch: any, sendEvent: any, connector: any, exchangeAddress: string) {
    if (!connector || !eddsaApi) {
        return
    }

    nokeyApi.getAccountWrapper(account.accAddr).then((accInfo) => {
        // 1 has id and pubkey
        if (accInfo.publicKey && accInfo.publicKey.x && accInfo.publicKey.y) {
            connector.getProvider().then((provider: any) => {
                const web3 = new Web3(provider as any)

                web3.eth.getCode(account.accAddr).then((code) => {
                    if (code === '0x') {
                        dispatch(setIsContractAddress(false))
                    } else {
                        dispatch(setIsContractAddress(true))
                    }

                    sign_tools
                        .generateKeyPair(
                            web3,
                            account.accAddr,
                            exchangeAddress,
                            accInfo.nonce - 1,
                            account.connectName,
                        )
                        .then(
                            (eddsakey: any) => {
                                const sk = toHex(toBig(eddsakey.keyPair.secretKey))
                                const px = toHex(toBig(eddsakey.keyPair.publicKeyX))
                                const py = toHex(toBig(eddsakey.keyPair.publicKeyY))

                                /*
                                console.log('accInfo publicKey:', accInfo.publicKey)
                                console.log('publicKeyX:', eddsakey.keyPair.publicKeyX, '*')
                                console.log('px:', px, '*')
                                console.log('publicKeyY:', eddsakey.keyPair.publicKeyY, '*')
                                console.log('py:', py, '*')
                                console.log('sk:', sk, '*')
                                */

                                eddsaApi.getApiKey(sk, accInfo.accountId).then((response: any) => {
                                    sendEvent(StatusChangeEvent.HasPubkey)
                                    dispatch(setEddsaKey(sk))
                                    dispatch(setAccountInfo(accInfo))
                                    dispatch(setApikey(response.apiKey))
                                }).catch((reason) => {
                                    dumpError400(reason)
                                    dispatch(setEddsaKey(sk))
                                    dispatch(setAccountInfo(accInfo))
                                    sendEvent(StatusChangeEvent.ErrorResponse)
                                })
                            },
                            (reject: any) => {
                                dumpError400(reject)
                                dispatch(setAccountInfo(accInfo))
                                sendEvent(StatusChangeEvent.ErrorResponse)
                            }
                        ).catch((reason) => {
                            dumpError400(reason)
                            sendEvent(StatusChangeEvent.ErrorResponse)
                        })

                })

            })
        }
        // 2 has id but no pubkey
        else {
            console.log('2 has id but no pubkey currStatus:', account.status)
            sendEvent(StatusChangeEvent.HasNoPubkey)
        }
    }, (reason) => {
        // 3 has none of above
        dumpError400(reason)
        // check deposit tx (local and ethereum)
        sendEvent(StatusChangeEvent.ErrorResponse)
    })
}

export function useCheckAccStatus() {

    const dispatch = useDispatch()

    const resetFunc = () => {
        dispatch(reset())
        sendEvent(StatusChangeEvent.Reset)
    }

    const nokeyApi = useAuthNoKeyApiInstance()

    const ecdsaApi = useAuthHasEcdsaSigApiInstance()

    const eddsaApi = useAuthHasEddsaSigApiInstance()

    const { account: web3Account, active, isConnected, chainId, connector, } = useWeb3Account()

    const prevChainId = usePrevious(chainId)
    const prevWeb3Account = usePrevious(web3Account)

    // console.log('prevChainId:', prevChainId, ' chainId:', chainId)
    // console.log('prevWeb3Account:', prevWeb3Account, ' web3Account:', web3Account)

    const { account, sendEvent } = useStateMachine()

    const { exchangeInfo } = useGetTradingInfo()

    useEffect(() => {
        if (((prevChainId && chainId && prevChainId !== chainId) || (prevWeb3Account && web3Account && prevWeb3Account !== web3Account))
            && account.status !== AccountStatus.UNCONNNECTED) {
            resetFunc()
            return
        }

        if (!exchangeInfo?.exchangeAddress) {
            return
        }

        const exchangeAddress: string = exchangeInfo?.exchangeAddress

        switch (account.status) {
            case AccountStatus.UNCONNNECTED:
                // console.log('---> render UNCONNNECTED active:', active, ' isConnected:', isConnected)

                if (isConnected) {
                    dispatch(setConnectNameTemp(ConnectorNames.Injected))
                    sendEvent(StatusChangeEvent.Connecting)
                }
                break

            case AccountStatus.CONNECTED:
                //check session or local storage
                dispatch(setAccAddr(web3Account as string))
                dispatch(setConnectName(account.connectNameTemp))

                // console.log('---> render CONNECTED account.accAddr:', account.accAddr)

                if (account.accAddr && account.apiKey) {
                    sendEvent(StatusChangeEvent.HasPubkey)
                } else if (account.accAddr) {
                    // else try getAccount
                    checkAccountAvailable(nokeyApi, ecdsaApi, eddsaApi, account, dispatch, sendEvent, connector, exchangeAddress)
                } else {
                    console.log('no acc')
                }
                break

            case AccountStatus.NOACCOUNT:
                /*
                const handler = setTimeout(() => {
                    console.log('NOACCOUNT time fetch! ' , account)
                    checkAccountAvailable(account, dispatch, sendEvent)
                    if (account.status !== AccountStatus.NOACCOUNT) {
                        console.log('NOACCOUNT status change to ', account.status)
                        clearInterval(handler)
                    }
                }, POLLING_INTERVAL)'
                */
                break

            case AccountStatus.DEPOSITING:
                // TODO sub contract event, check deposit.
                const depositFinished = true // TODO
                if (depositFinished) {
                    console.log('depositFinished currStatus:', account.status)
                    sendEvent(StatusChangeEvent.FinishDeposit)
                }
                break

            case AccountStatus.DEPOSIT_TO_CONFIREM:
                // getAccount every 10s to check
                const deposit_check = setTimeout(() => {
                    console.log('DEPOSIT_TO_CONFIREM time fetch! ', account)
                    checkAccountAvailable(nokeyApi, ecdsaApi, eddsaApi, account, dispatch, sendEvent, connector, exchangeAddress)
                    if (account.status !== AccountStatus.DEPOSIT_TO_CONFIREM) {
                        console.log('DEPOSIT_TO_CONFIREM status change to ', account.status)
                        clearInterval(deposit_check)
                    }

                }, POLLING_INTERVAL)
                break

            case AccountStatus.UNACTIVATED:
                const isSmartWallet = false // TODO
                if (isSmartWallet) {
                    // TODO approve hash,
                    console.log('approve hash, currStatus:', account.status)
                    sendEvent(StatusChangeEvent.IsSmartWallet)
                } else {
                    console.log('approve hash no smartwallet, account.status:', account.status)
                }
                break

            case AccountStatus.ARPROVING:
                // TODO sub contract event, check approving.
                const approved = false // TODO
                if (approved) {
                    console.log('sub contract event, check approving. approved! status:', account.status)
                    sendEvent(StatusChangeEvent.ApproveSubmit)
                }
                break

            case AccountStatus.APPROV_TO_CONFIRM:
                const approveConfirmed = false // TODO
                if (approveConfirmed) {
                    console.log('approveConfirmed status:', account.status)
                    sendEvent(StatusChangeEvent.ApproveConfirmed)
                }
                break

            case AccountStatus.LOCKED:
                break

            case AccountStatus.ACTIVATED:
                break

            default:
                break
        }

    }, [nokeyApi, ecdsaApi, eddsaApi, account, prevChainId, chainId, web3Account, dispatch, sendEvent, connector, exchangeInfo,])

}
