import i18n from 'plugins/i18n'

import LngSelectorUI from 'components/selects/LngSelector'

import { LangType } from 'defs/common_defs'

import  { useSettings }from 'stores/settings/hook'

const LngSelector = () => {

  const settings = useSettings()

  const onLngChange = (lan: any) => {
    i18n.changeLanguage(lan === LangType.cn ? LangType.cn : LangType.en)
    settings.setLngWrapper(lan)
  }
  
  const lng_data = [
    {
      'title': 'English',
      'value': LangType.en
    },
    {
      'title': '',
      'value': LangType.cn
    },
  ]

  return (
    <LngSelectorUI lng_data={lng_data} onLngChange={onLngChange} />
  )
}

export default LngSelector
