import styled from 'styled-components/macro'

export interface AccountBtnProps {
  txt?: string | null,
  w?: string,
  h?: string,
  onClick?: any,
}

const Button = styled.button<AccountBtnProps>`
cursor: pointer;
background: ${({ theme }) => theme.colors.cwBtnBgColor};
color: ${({ theme }) => theme.colors.cwBtnColor};
border-radius: 8px;
width: ${({ w }) => w ? w: '120px'};
height: ${({ h }) => h ? h: '36px'};
line-height: 36px;
text-align: center;

font-family: Roboto-Black;
font-size: 16px;
line-height: 19px;
`

const AccountBtn = ({ txt, onClick, w, h, }: AccountBtnProps) => {

  const accSubstr = (acc: any) => {
    if (typeof(acc) === 'string') {
      return acc.substr(0, 8) + '...' + acc.substr(acc.length - 6)
    }
    return ''
  }

  return (
    <Button onClick={onClick} w={w} h={h}>
      {accSubstr(txt)}
    </Button>
  )
}

export default AccountBtn
