import React from 'react'
import styled from 'styled-components/macro'

const Styles = styled.div`

  overflow-y: scroll;
  height: 100%;
  
  &::-webkit-scrollbar {
    display:none;
  }

  table {
    border-spacing: 0;
  
    height: 500px;

    tr {
      :last-child {
        td {
          border-bottom: 0;
        }
      }
    }

    th,
    td {
      margin: 0;
      padding-top: 3px;

      text-align: right;

      :last-child {
      }
    }

    .ab_col1 {
      float: left;
      width: 40%;
    }
  
    .ab_col2 {
      padding-left: 20px;
      float: left;
      width: 30%;
    }
  
    .ab_col3 {
      padding-left: 20px;
      float: left;
      width: 30%;
    }
  
    .ask_row {
      color: #00BBA8;
    }

    .bid_row {
      color: #FF5677;
    }

  }
`
export interface OrderBookTableProps {
  depth: any
}

const Table = ({ depth }: OrderBookTableProps) => {

  return (
    <Styles>
      <table>
        <thead>
        </thead>
        <tbody>
          {
            depth && depth.asks &&
            depth.asks.map((ask: any, ask_index: number) => {
              return (
                <tr className={'ask_row'} key={ask_index}>
                  <td className={'ab_col1'}>{ask.price}</td>
                  <td className={'ab_col2'}>{ask.amt.toFixed(4)}</td>
                  <td className={'ab_col3'}>{ask.amtTotal.toFixed(4)}</td>
                </tr>
              )
            })
          }
          {
            depth && depth.bids &&
            depth.bids.map((bid: any, bid_index: number) => {
              return (
                <tr className={'bid_row'} key={bid_index}>
                  <td className={'ab_col1'}>{bid.price}</td>
                  <td className={'ab_col2'}>{bid.amt.toFixed(4)}</td>
                  <td className={'ab_col3'}>{bid.amtTotal.toFixed(4)}</td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </Styles>
  )
}

export default Table
