import { useTranslation } from 'react-i18next'

import { Tabs, Tab, Content } from 'components/tabs/HTabs'
import TradeRecordTable from 'components/tables/TradeRecordTable'

import { useGetTradingInfo } from 'stores/trading/hook'
import { useTrades } from 'hooks/exchange/useAuthNoKeyApi'
import { useTabs } from 'hooks/tabHook/useTabs'

const TradeRecordsPanel = () => {

    const { t } = useTranslation()

    const { tradingInfo } = useGetTradingInfo()
  
    const symbol_quote = tradingInfo.symbol

    const { trades, } = useTrades(symbol_quote)

    const { active, handleClick, } = useTabs()

    const cols = [
        {
            Header: t('pages:trade_col_1') + '(' + symbol_quote + ')',
            accessor: 'trade_col_1',
        },
        {
            Header: t('pages:trade_col_2'),
            accessor: 'trade_col_2',
        },
        {
            Header: t('pages:trade_col_3'),
            accessor: 'trade_col_3',
        },
    ]

    return (
        <div className='draggable_panel'>
            <div className='draggable_header' >
                {t('pages:trade_record_title')}
            </div>

            <Tabs>
                <Tab onClick={handleClick} active={active === '0'} id={'0'}>
                    {t('pages:trade_record_latest')}
                </Tab>

                <Tab onClick={handleClick} active={active === '1'} id={'1'}>
                    {t('pages:trade_record_mine')}
                </Tab>
            </Tabs>
            <>
                <Content active={active === '0'}>
                    <TradeRecordTable cols={cols} trades={trades}/>
                </Content>
                <Content active={active === '1'}>
                    <TradeRecordTable cols={cols} trades={trades}/>
                </Content>
            </>
        </div>
    )
}

export default TradeRecordsPanel
