import React from 'react'
import styled from 'styled-components/macro'

const Styles = styled.div`

  overflow-y: scroll;
  height: 95%;
  
  &::-webkit-scrollbar {
    display:none;
  }

  table {
    border-spacing: 0;
  
    height: 500px;

    tr {
      :last-child {
        td {
          border-bottom: 0;
        }
      }
    }

    th,
    td {
      margin: 0;
      padding-top: 3px;

      text-align: right;

      :last-child {
      }
    }

    .ask_row {
      color: #00BBA8;
    }

    .bid_row {
      color: #FF5677;
    }

  }
`
export interface QuoteTableProps {
  quotes: any
  cols: any[]
}

const Table = ({ quotes, cols }: QuoteTableProps) => {

  return (
    <Styles>
      <table>
        <thead>
          <tr>
            {
              cols.map((col: any, order_index: number) => {
                return (
                  <th key={order_index}>
                    {col.Header}
                  </th>
                )
              })
            }
          </tr>
        </thead>
        <tbody>
          {
            quotes &&
            quotes.map((quote: any, quote_index: number) => {
              return (
                <tr key={quote_index}>
                  <td>{quote}</td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </Styles>
  )
}

export default Table
