import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'

import { useTheme } from 'styled-components'
import styled from 'styled-components/macro'

import TradingChart from 'components/charts/TradingChart'

import { authNoKeyApi } from '@loopring-web/sdk/lightcone_api/api_wrapper'

import { genOHLC, genOption, genDepthOption } from 'utils/kline_tools'

import { useGetTradingInfo } from 'stores/trading/hook'

import { Tabs, Tab, Content } from 'components/tabs/HTabs'

import { REFRESH_RATE } from '@loopring-web/sdk/defs/exchange_defs'

import { useTabs } from 'hooks/tabHook/useTabs'

const Title = styled.div`
    float: left;
`

export interface ChartWrapperProps {
    depth: any
}

const TradingChartPanel = ({ depth }: ChartWrapperProps) => {

    const theme: any = useTheme()

    const upColor = theme.colors.upColor
    const upBorderColor = theme.colors.upBorderColor
    const downColor = theme.colors.downColor
    const downBorderColor = theme.colors.downBorderColor

    const { tradingInfo } = useGetTradingInfo()

    const { t } = useTranslation()

    const { active, handleClick, } = useTabs()

    const getOption = async () => {
        const data1 = await authNoKeyApi.getCandlestick(tradingInfo.symbol, tradingInfo.interval)
        var data = []
        if (data1.status === 200) {
            data = genOHLC(data1.data['candlesticks'])
        }
        return genOption(data, upColor, upBorderColor, downColor, downBorderColor)
    }

    const getDepthOption = () => {
        return genDepthOption(depth, upColor, downColor)
    }

    const defaultOption = genOption([], upColor, upBorderColor, downColor, downBorderColor)
    const defaultDepthOption = genDepthOption(depth, upColor, downColor)

    return (
        <div className='draggable_panel'>
            <div className='draggable_header' >
                <Title>{t('pages:chart_title')}</Title>
            </div>

            <Tabs>
                <Tab onClick={handleClick} active={active === '1'} isRight={true} id={'1'}>
                    {t('pages:chart_toggle_right')}
                </Tab>
                <Tab onClick={handleClick} active={active === '0'} isRight={true} id={'0'}>
                    {t('pages:chart_toggle_left')}
                </Tab>
            </Tabs>

            <>
                <Content active={active === '0'}>
                    <TradingChart refreshRate={REFRESH_RATE} defaultOption={defaultOption} getOption={getOption} />
                </Content>
                <Content active={active === '1'}>
                    <TradingChart refreshRate={REFRESH_RATE} defaultOption={defaultDepthOption} getOption={getDepthOption} />
                </Content>
            </>
        </div>
    )
}

export default TradingChartPanel
