import { createSlice, PayloadAction } from '@reduxjs/toolkit'

import { ExchangeInfo, TokenInfo } from '@loopring-web/sdk/defs/exchange_defs'

export enum TradingInterval {
  min1 = '1min',
  min5 = '5min',
  min15 = '15min',
  min30 = '30min',
  hr1 = '1hr',
  hr2 = '2hr',
  hr4 = '4hr',
  hr12 = '12hr',
  d1 = '1d',
  w1 = '1w',
}

export interface TradingInfo {
  symbol: string
  symbol_base: string
  symbol_quote: string
  interval: TradingInterval
  markets: any
  tokens: {[key: string]: [value: TokenInfo]}
  exchangeInfo?: ExchangeInfo
  wsApiKey: string
}

const getBaseQuote = (symbol: string) => {
  const ind = symbol.indexOf('-')
  const symbol_base = symbol.substr(ind + 1)
  const symbol_quote = symbol.substr(0, ind)
  return {symbol_base, symbol_quote}
}

const defaultSymbol = process.env.REACT_APP_TEST_SYMBOL ? process.env.REACT_APP_TEST_SYMBOL : 'LRC-USDT'
const defaultInterval = process.env.REACT_APP_TEST_INTERVAL ? process.env.REACT_APP_TEST_INTERVAL : TradingInterval.hr1

const { symbol_base, symbol_quote } = getBaseQuote(defaultSymbol)

const initialState = {
  symbol: defaultSymbol,
  symbol_base: symbol_base,
  symbol_quote: symbol_quote,
  interval: defaultInterval,
  markets: [],
  tokens: {},
  refreshRate: 1000,
  refreshRateSlow: 3000,
  wsApiKey: '',
} as TradingInfo

const tradingSlice = createSlice({
  name: 'trading',
  initialState,
  reducers: {
    changeSymbol(state, action: PayloadAction<any>) {
      state.symbol = action.payload
      const { symbol_base, symbol_quote } = getBaseQuote(state.symbol)
      state.symbol_base = symbol_base
      state.symbol_quote = symbol_quote
    },
    changeTradingInterval(state, action: PayloadAction<any>) {
      state.interval = action.payload
    },
    setWsApiKey(state, action: PayloadAction<string>) {
      state.wsApiKey = action.payload
    },
    setMarkets(state, action: PayloadAction<any>) {
      state.markets = action.payload
    },
    setTokens(state, action: PayloadAction<{}>) {
      state.tokens = action.payload
    },
    setExchangeInfo(state, action: PayloadAction<ExchangeInfo>) {
      state.exchangeInfo = action.payload
    },
  },
})

export const { changeSymbol, changeTradingInterval, setWsApiKey, setMarkets, setTokens, setExchangeInfo, } = tradingSlice.actions
export default tradingSlice
